import React from 'react';

import empty from '../img/checkbox_empty.svg';
import indeterminate from '../img/checkbox_indeterminate.svg';
import checked from '../img/checkbox_checked.svg';

import styles from '../css/Checkbox.module.css';

function Checkbox(props) {
    if (props.checked === true || props.checked === 'checked') {
        return (<img className={styles.root} src={checked} onClick={props.onChange} alt="" />);
    }

    if (props.checked === 'indeterminate') {
        return (<img className={styles.root} src={indeterminate} onClick={props.onChange} alt="" />);
    }

    return (<img className={styles.root} src={empty} onClick={props.onChange} alt="" />);
}

export default Checkbox;
