function parseCsv(data) {
    const contacts = data.split(/\r?\n/)
        .map((line) => line.split(','));
    const columns = {
        first: contacts[0].indexOf('first'),
        last: contacts[0].indexOf('last'),
        phone: contacts[0].indexOf('phone'),
        email: contacts[0].indexOf('email')
    };
    return contacts.splice(1)
        .map((contact) => ({
            first: contact[columns.first],
            last: contact[columns.last],
            phone: contact[columns.phone],
            email: contact[columns.email]
        })
    );
}

function parseVcf(data) {
    const contacts = data.split(/\r?\n/)
        // Split array at END:VCARD
        .map((line) => line.split(':'));
}

export { parseCsv };
