import React, { useState } from 'react';

import Modal from './Modal';

import { ReactComponent as Close } from '../img/close.svg';

import styles from '../css/Editor.module.css';
import common from '../css/Common.module.css';

const TextInput = (props) => (
    <input
        className={styles.text}
        type="text"
        {...props}
    />
);

function Editor(props) {
    const [data, setData] = useState(props.data);
    const update = (field, value) => setData({ ...data, [field]: value });
    const onChange = (field, regex) => (e) => {
        if (regex && !regex.test(e.target.value)) return;
        update(field, e.target.value);
    };

    const createState = (field, regex) => ({
        onChange: onChange(field, regex),
        value: data[field]
    });

    const save = () => props.onSave(data);

    return (
        <Modal>
            <div>
                <div className={styles.section}>
                    <div className={[styles.row, styles.right].join(' ')}>
                        <Close className={common.action} onClick={props.onCancel} />
                    </div>
                </div>
                <div className={styles.section}>
                    <div className={styles.header}>Name</div>
                    <div className={styles.row}>
                        <TextInput placeholder="First" {...createState('first')} />
                        <TextInput placeholder="Last" {...createState('last')} />
                    </div>
                </div>
                <div className={styles.section}>
                    <div className={styles.header}>Contact</div>
                    <div className={styles.row}>
                        <TextInput placeholder="Phone Number" {...createState('phone', /^\d{0,10}$/)} />
                        <TextInput placeholder="Email" {...createState('email')} />
                    </div>
                </div>
                <div className={styles.section}>
                    <div className={[styles.row, styles.right].join(' ')}>
                        <div className={styles.button} onClick={props.onCancel}>Cancel</div>
                        <div className={[styles.button, styles.primary].join(' ')} onClick={save}>Save</div>
                    </div>
                </div>
            </div>
        </Modal>
    );
}

export default Editor;
