import React, { useState } from 'react';

import Checkbox from './Checkbox';
import Editor from './Editor';

import { ReactComponent as Add } from '../img/add.svg';
import { ReactComponent as Edit } from '../img/edit.svg';
import { ReactComponent as Remove } from '../img/remove.svg';

import styles from '../css/List.module.css';
import common from '../css/Common.module.css';

const formatPhoneNumber = (n) => `(${n.substring(0,3)}) ${n.substring(3,6)}-${n.substring(6,10)}`;

const emptyContact = {
    first: '',
    last: '',
    phone: '',
    email: ''
};

function Person(props) {
    const [editing, setEditing] = useState(false);
    const onCheckChange = () => {
        props.onChange({
            ...props.data,
            selected: !props.data.selected
        });
    };

    const startEditing = () => setEditing(true);
    const cancelEditing = () => setEditing(false);
    const finishEditing = (data) => {
        props.onChange(data);
        setEditing(false);
    };

    return (
        <div className={props.data.selected ? [styles.row, styles.selected].join(' ') : styles.row}>
            <div className={styles.element} >
                <Checkbox checked={props.data.selected} onChange={onCheckChange} />
            </div>
            <div className={styles.element}>{props.data.first} {props.data.last}</div>
            <div className={styles.element}>{formatPhoneNumber(props.data.phone)}</div>
            <div className={styles.element}>{props.data.email}</div>
            <div className={styles.element}>
                <Edit className={common.action} onClick={startEditing}/>
                <Remove className={common.action} onClick={props.onRemove} />
            </div>
            { editing && <Editor data={props.data} onCancel={cancelEditing} onSave={finishEditing} /> }
        </div>
    );
};

function List(props) {
    const [editing, setEditing] = useState(false);
    const startEditing = () => setEditing(true);
    const cancelEditing = () => setEditing(false);
    const finishEditing = (data) => {
        props.onUpdate(-1, data);
        setEditing(false);
    };

    return (
        <div className={styles.root}>
            <div className={styles.table}>
                <div className={styles.header}>
                    <div className={styles.element}>
                        <Checkbox checked={props.selected} onChange={props.onCheck} />
                    </div>
                    <div className={styles.element}>Name</div>
                    <div className={styles.element}>Phone Number</div>
                    <div className={styles.element}>Email</div>
                    <div className={styles.element}><Add className={styles.action} onClick={startEditing} /></div>
                </div>
                <div className={styles.body}>
                { props.people.map((x,i) => <Person key={i} data={x} onChange={(d) => props.onUpdate(i,d)} onRemove={() => props.onRemove(i)} />) }
                </div>
                { editing && <Editor data={emptyContact} onCancel={cancelEditing} onSave={finishEditing} /> }
            </div>
        </div>
    );
}

export default List;
