import React from 'react';

import styles from '../css/Sidebar.module.css';

import { createCsv, createVcf, generateDownload } from '../util/exporters';

function Sidebar(props) {
    const exportVcf = () => {
        let contacts = props.data;
        if (contacts.some((contact) => contact.selected)) {
            contacts = contacts.filter((x) => x.selected);
        }
        let output = createVcf(contacts);
        generateDownload('test.vcf', output);
    };

    const exportCsv = () => {
        let contacts = props.data.filter((x) => x.selected);
        let output = createCsv(contacts);
        generateDownload('test.csv', output);
    }

    return (
        <div className={styles.root}>
            <div className={styles.header}>Export</div>
            <div className={styles.item} onClick={exportVcf}>Export as VCF</div>
            <div className={styles.item} onClick={exportCsv}>Export as CSV</div>
        </div>
    );
}

export default Sidebar;
