import React, { useState, useEffect } from 'react';

import List from './components/List';
import Sidebar from './components/Sidebar';

import styles from './css/App.module.css';
import readFile from './util/readFile';
import { parseCsv } from './util/parsers';

function App() {
    const [data, setData] = useState(JSON.parse(localStorage.getItem('data') || '[]'));
    useEffect(() => localStorage.setItem('data', JSON.stringify(data)), [data]);

    const update = (index, newData) => {
        let next = data.slice();
        if (index === -1) {
            next.push(newData);
        } else {
            next[index] = newData;
        }
        setData(next);
    };

    const remove = (index) => {
        let next = data.slice();
        next.splice(index, 1);
        setData(next);
    };

    // Check if no one is selected
    let none = !data.some((x) => x.selected);
    // If no one is selected, it cannot be that everyone is selected
    let all = !none && data.every((x) => x.selected);
    let selected = !all && !none ? 'indeterminate' : all;
    const changeCheck = () => {
        if (selected === true) {
            setData(data.map((x) => ({ ...x, selected: false })));
        } else {
            setData(data.map((x) => ({ ...x, selected: true })));
        }
    }

    const onDragEnter = (e) => {
        e.preventDefault();
    };
    const onDragOver = (e) => {
        e.preventDefault();
    };
    const onDrop = (e) => {
        e.preventDefault();
        if (e.dataTransfer.items) {
            let files = [...e.dataTransfer.items]
                .filter((item) => item.kind === 'file')
                .map((item) => item.getAsFile())
                .filter((file) => file.type === 'text/csv');
            if (files.length > 0) {
                readFile(files[0])
                .then((contents) => parseCsv(contents))
                .then((imported) => setData([...data, ...imported]));
            }
        }
    };

    return (
        <div className={styles.root} onDragEnter={onDragEnter} onDragOver={onDragOver} onDrop={onDrop}>
            <Sidebar data={data} />
            <List
                people={data}
                selected={selected}
                onUpdate={update}
                onRemove={remove}
                onCheck={changeCheck}
            />
        </div>
    );

    // return (
    //     <div className={[styles.root, styles.drop].join(' ')} onDragEnter={onDragEnter} onDragOver={onDragOver} onDrop={onDrop}>

    //     </div>
    // );
}

export default App;
