function createCsv(data) {
    let output = 'First Name,Last Name,Phone,Email\n';
    for (let i = 0; i < data.length; i++) {
        output += `${data[i].first},${data[i].last},${data[i].phone},${data[i].email}\n`;
    }
    return output;
}

function createVcf(data) {
    let output = '';
    for (let i = 0; i < data.length; i++) {
        output += 
`BEGIN:VCARD
VERSION:4.0
N:${data[i].last};${data[i].first};;;
FN:${data[i].first} ${data[i].last}
TEL;TYPE=cell:${data[i].phone}
EMAIL:${data[i].email}
END:VCARD
`;
    }
    return output;
}

const mimeTypes = {
    vcf: 'text/vcard',
    csv: 'text/csv'
};

function generateDownload(filename, content) {
    const mime = mimeTypes[filename.slice(-3)];
    const e = document.createElement('a');
    e.setAttribute('href', `data:${mime},` + encodeURIComponent(content));
    e.setAttribute('download', filename);
    e.style.display = 'none';
    document.body.appendChild(e);
    e.click();
    document.body.removeChild(e);
}

export { createCsv, createVcf, generateDownload };
